.flex-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}
.flex-item {
    max-width: 80%;
}

.auto-ul {
    list-style-type: none;
}
.auto-li:nth-of-type(1n+9) {
    display: none;
}

@media screen and (max-width: 480px) {
    .auto-li:nth-of-type(1n+4) {
        display: none;
    }
}
.suggest-box {
    border: 2px solid #7fb1e4;
    text-align: left;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.result-ul {
  list-style-type: circle;
  background: #f2f2f2;
  border-radius: 5px;
  padding-top: 5px;
}

.result-ul span {
  margin-top: 20px;
  padding-left: 30px;
}
.result-ul .result-li {
  margin: 20px;
  padding: 5px;
}

input[type=text] {
  width: 100%;
  padding: 12px 40px;
  margin: 10px 0 0 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}




